import './calculator.css';
import React, { useReducer } from 'react';
import DigitButton from './DigitButton';
import OperationButton from './OperationButton';

export const ACTIONS = {
    ADD_DIGIT: 'add_digit',
    CHOOSE_OPERATION: 'choose_operation',
    CLEAR: 'clear',
    EVALUATE: 'evaluate',
    DELETE: 'delete'
}

function evaluate({previousOperand, currentOperand, operation}) {
    const prev = parseFloat(previousOperand);
    const curr = parseFloat(currentOperand);
    if (isNaN(prev) || isNaN(curr)){
        return '';
    }
    let result;
    switch(operation) {
        case '÷':
            result = prev / curr;
            break;
        case '+':
            result = prev + curr;
            break;
        case '*':
            result = prev * curr;
            break;
        case '-':
            result = prev - curr;
            break;
        default:
            result = '';
    }

    return result.toString();
}

function reducer(state, {type, payload}) {
    switch(type) {
        case ACTIONS.ADD_DIGIT:
            if (state.overwrite) {
                return {
                    ...state,
                    currentOperand: `${payload.digit}`,
                    overwrite: false
                }
            }
            if (payload.digit === '.' && state.currentOperand.includes('.')){
                return state;
            } else if (payload.digit === '0' && state.currentOperand === '0') {
                return state;
            }

            return {
                ...state, 
                currentOperand: `${state.currentOperand || ''}${payload.digit}`
            }
            
        case ACTIONS.CHOOSE_OPERATION:
            if(state.currentOperand == null && state.previousOperand == null) {
                return state;
            }
            if(state.previousOperand == null) {
                return {
                    ...state, 
                    operation: payload.operation,
                    previousOperand: state.currentOperand,
                    currentOperand: null
                }
            }
            if(state.currentOperand == null) {
                return {
                    ...state,
                    operation: payload.operation,
                }
            }

            return {
                ...state,
                operation: payload.operation,
                previousOperand: evaluate(state),
                currentOperand: null
            }

        case ACTIONS.CLEAR:
            return {}

        case ACTIONS.EVALUATE:
            if (state.previousOperand == null || state.operation == null || state.currentOperand == null) {
                return state;
            }
            return {
                ...state,
                overwrite: true,
                currentOperand: evaluate(state),
                operation: null,
                previousOperand: null
            }

        case ACTIONS.DELETE:
            if (state.overwrite) {
                return {
                    ...state,
                    currentOperand: null,
                    overwrite: false
                }
            }

            if (state.currentOperand == null) {
                return state;
            } else if (state.currentOperand.length === 1){
                return {
                    ...state,
                    currentOperand: null
                }
            }
            return {
                ...state,
                currentOperand: state.currentOperand.slice(0, -1)
            }

        default:
            return state;
    }
}

const INTEGER_FORMATTER = new Intl.NumberFormat('en-us', {
    maximumFractionDIgits: 0
})

function formatInteger(operand) {
    if (operand == null) return;
    const [integer, decimal] = operand.split('.');
    if (decimal == null) return INTEGER_FORMATTER.format(integer);
    return `${INTEGER_FORMATTER.format(integer)}.${decimal}`
}

function Calculator() {
    const [{previousOperand, currentOperand, operation}, dispatch] = useReducer(reducer, {} );

    return (
        <div className="container calculator-body">
            <div className="calculator-grid">
                <div className="output">
                    <div className="previous-operand">{formatInteger(previousOperand)} {operation}</div>
                    <div className="current-operand">{formatInteger(currentOperand)}</div>
                </div>
                <button onClick={() => dispatch({type: ACTIONS.CLEAR})} className="span-two">AC</button>
                <button onClick={() => dispatch({type: ACTIONS.DELETE})}>DEL</button>
                <OperationButton operation="÷" dispatch={dispatch} />
                <DigitButton digit='1' dispatch={dispatch} />
                <DigitButton digit='2' dispatch={dispatch} />
                <DigitButton digit='3' dispatch={dispatch} />
                <OperationButton operation="*" dispatch={dispatch} />
                <DigitButton digit='4' dispatch={dispatch} />
                <DigitButton digit='5' dispatch={dispatch} />
                <DigitButton digit='6' dispatch={dispatch} />
                <OperationButton operation="+" dispatch={dispatch} />
                <DigitButton digit='7' dispatch={dispatch} />
                <DigitButton digit='8' dispatch={dispatch} />
                <DigitButton digit='9' dispatch={dispatch} />
                <OperationButton operation="-" dispatch={dispatch} />
                <DigitButton digit='.' dispatch={dispatch} />
                <DigitButton digit='0' dispatch={dispatch} />
                <button onClick={() => dispatch({type:ACTIONS.EVALUATE})} className="span-two">=</button>
            </div>
        </div>
    )
}

export default Calculator;