import React from 'react';
import { Nav, Navbar, NavbarBrand, NavbarToggler, Collapse, NavItem, Button, Modal, ModalHeader, ModalBody, Form, FormGroup, Input, Label } from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            isNavOpen: false,
            isModalOpen: false,
            username: '',
            password: '',
            remember: false
        }
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
    }

    toggleNav() {
        this.setState({
            isNavOpen: !this.state.isNavOpen
        })
    }

    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        })
    }

    handleLogin(event) {
        this.toggleModal();
        alert("Username: " + this.state.username + " Password: " + this.state.password
            + " Remember: " + this.state.remember + 'This is a Front-end project so log in feature is not completely developed');
        event.preventDefault();
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.value === 'checkbox'? target.checked : target.value;
        const name = target.name;
        this.setState({
            [name]: value
        })
    }

    render() {
        return(
            <div>
                <Navbar dark expand="md">
                    <NavbarBrand  id="cs50text"  href="/">CS50 Final Project</NavbarBrand>
                    <NavbarToggler onClick={this.toggleNav} />
                    <Collapse isOpen={this.state.isNavOpen} navbar>
                        <Nav navbar>
                            <NavItem>
                                <NavLink className="nav-link" to="/home">Home</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to='/calculator'>Calculator</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link"  to='/tictactoe'>TicTacToe</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to='/weather'>Weather</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to='/currency'>Currency</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className="nav-link" to='/about'>About</NavLink>
                            </NavItem>
                        </Nav>
                        <Nav className="ms-auto" navbar>
                            <NavItem>
                                <Button id="loginbutton" onClick={this.toggleModal}><span className="fa fa-sign-in fa-lg"></span>Login</Button>
                            </NavItem>
                        </Nav>
                    </Collapse>
                </Navbar>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader>Login</ModalHeader>
                    <ModalBody>
                        <Form onSubmit={this.handleLogin}>
                            <FormGroup>
                                <Label htmlFor="username">Username</Label>
                                <Input type="text" id="username" name="username"
                                onChange={this.handleInputChange} />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password"
                                onChange={this.handleInputChange} />
                            </FormGroup>
                            <FormGroup>
                                <Label>
                                    <input type="checkbox" name="remember"
                                onChange={this.handleInputChange}/>
                                Remember me
                                </Label>
                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Login</Button>
                        </Form>
                    </ModalBody>
                </Modal>
            </div>
        )
    }
}

export default Header;