import React from 'react';
import './tictactoe.css';
import {Dropdown, DropdownMenu, DropdownItem, DropdownToggle} from 'reactstrap';

function Square(props) {
      return (
        <button className="square" 
        onClick = {() => props.onClick()}
        style = {props.value === 'X'? {color : 'red'} : {color : 'blue'}}>
          { props.value }
        </button>
      );
}
  
function calculateWinner(squares){
    const winningPatterns = [
        [0, 1, 2],
        [3, 4, 5],
        [6, 7, 8],
        [0, 4, 8],
        [2, 4, 6],
        [0, 3, 6],
        [1, 4, 7],
        [2, 5, 8]
    ];
    for(let pattern of winningPatterns){
        let [a, b, c] = pattern;
        if(squares[a] && squares[a] === squares[b] && squares[a] === squares[c]){
            return squares[a];
        }
    }
    return null;
}

class Board extends React.Component {


    renderSquare(i) {
      return (<Square 
      value={this.props.squares[i]}
      onClick = {() => this.props.onClick(i)}
      />);
    }
  
    render() {

      return (
        <div>
          <div className="board-row">
            {this.renderSquare(0)}
            {this.renderSquare(1)}
            {this.renderSquare(2)}
          </div>
          <div className="board-row">
            {this.renderSquare(3)}
            {this.renderSquare(4)}
            {this.renderSquare(5)}
          </div>
          <div className="board-row">
            {this.renderSquare(6)}
            {this.renderSquare(7)}
            {this.renderSquare(8)}
          </div>
        </div>
      );
    }
  }
  
  class Tictactoe extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            history: [
                {squares: Array(9).fill(null)}
            ],
            xIsNext: true,
            stepNumber: 0,
            isDropdownOpen: false
        };
        this.handleClick = this.handleClick.bind(this);
        this.toggleDropdown = this.toggleDropdown.bind(this);
    }

    handleClick(i){
        const history = this.state.history.slice(0, this.state.stepNumber + 1);
        const current = history[history.length - 1];
        const squares = current.squares.slice();
        if(calculateWinner(squares) || squares[i]){
            return;
        }
        squares[i] = this.state.xIsNext? 'X' : 'O';
            
        this.setState({history: history.concat([{squares: squares}]), xIsNext: !this.state.xIsNext, stepNumber: history.length});
    };

    jumpTo(step){
        this.setState(
            {stepNumber: step,
            xIsNext: (step % 2) === 0}
        )
    }

    toggleDropdown() {
        console.log('function runs')
        this.setState({
            isDropdownOpen: !this.state.isDropdownOpen
        })
    }

    render() {
      const history = this.state.history;
      const current = history[this.state.stepNumber];
      const winner = calculateWinner(current.squares);
      const moves = history.map((step, move) => {
          const desc = move? 'Go to move#' + move : 'Go to game start';
          return(
              <DropdownItem id={move} onClick={() => this.jumpTo(move)}>
                  {desc}
              </DropdownItem>
              
          )
      })

      let status;
      if(winner){
          status = 'Congratulations, ' + winner + ' wins.';
      } else {
          status = 'Next player: ' + (this.state.xIsNext? 'X' : 'O');
      }

      return (
        <div className="game">
          <div className="game-board">
            <Board squares = {current.squares} onClick = {(i) => this.handleClick(i)}/>
          </div>
          <div className="game-info">
            <div className="status">{status}</div>
            <Dropdown toggle={this.toggleDropdown} isOpen={this.state.isDropdownOpen}>
                <DropdownToggle caret id="dropdown-basic">
                    Go Back
                </DropdownToggle>

                <DropdownMenu>
                    {moves}
                </DropdownMenu>
            </Dropdown>
          </div>
        </div>
      );
    }
  }
export default Tictactoe;