function Home() {
    return(
        <div className="container">
            <div className="row justify-content-evenly" id='margin-bottom'>
                <div className="card text-white bg-dark col-11 col-md-5">
                    <a href='/calculator'>
                        <h4 className="card-header">
                            Calculator
                        </h4>
                        <div className="card-body">
                            <h5 className="card-title">CSS Grid &amp; Unidirectional Data Flow</h5>
                            <p className="card-text">The style of the calculator is built using CSS-Grid. The underlying logic is based
                            on utilization of reducer, dispatcher and action from react. Upon pressing buttons, the values are passed 
                            via action.payload and the methods via action.type.</p>
                        </div>
                    </a>
                </div>
                <div className="card text-white bg-dark col-11 col-md-5">
                    <a href='/tictactoe'>
                        <h4 className="card-header">
                            Tic-tac-toe
                        </h4>
                        <div className="card-body">
                            <h5 className="card-title">React States &amp; Props</h5>
                            <p className="card-text">UI is simply built with HTML and CSS. This project is actually inspired by
                            tutorial from react official website. The states are handed over to the parent Tictactoe component to be able to
                            detect winner conveniently and also to track history data.</p>
                        </div>
                    </a>
                </div>
                <div className="card text-white bg-dark col-11 col-md-5">
                    <a href='/weather'>
                        <h4 className="card-header">
                            Weather
                        </h4>
                        <div className="card-body">
                            <h5 className="card-title">API &amp; Fetching Data</h5>
                            <p className="card-text">This mini-project is solely for API calling, using axios and data fetching. A search-box
                            is placed at the middle using CSS flex-box. The user can type in any city-name and it provides country short-form,
                            temperature and brief weather condion of the city.</p>
                        </div>
                    </a>
                </div>
                <div className="card text-white bg-dark col-11 col-md-5">
                    <a href='/currency'>
                        <h4 className="card-header">
                            Currency converter
                        </h4>
                        <div className="card-body">
                            <h5 className="card-title">API fetching &amp; useEffect</h5>
                            <p className="card-text">This currency converter is also bulit upon API calling and fetching json data, but with
                            vanilla javascript. The API site requires monthly subscription to call base currencies other than EUR but you can 
                            convert freely with some JS logic.</p>
                        </div>
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Home;