import React from 'react';

export default function CurrencyRow(props){
    const {
        currencyOptions,
        selectedCurrency,
        onChange,
        amount,
        onAmountChange
    } = props;

    const options = [];
    for (let key in currencyOptions){
        options.push(<option key={key} value={key}>{key+' ('+currencyOptions[key]+')'}</option>)
    }
    return(
        <div>
            <input type='number' className='currency-input' value={amount} onChange={onAmountChange}/>
            <select value={selectedCurrency} onChange={onChange}>
                {options}
            </select>
        </div>
    )
}
