import './currency.css';
import React, {useEffect, useState} from 'react';
import CurrencyRow from './CurrencyRow';

const BASE_URL = 'http://api.exchangeratesapi.io/v1/';
const API_KEY = '2eb162b5bee607e339316382ae6a1e2d';

function Currency() {

    const [currencyOptions, setCurrencyOptions] = useState([]);
    const [fromCurrency, setFromCurrency] = useState();
    const [toCurrency, setToCurrency] = useState();
    const [exchangeRate, setExchangeRate] = useState();
    const [exchangeRates, setExchangeRates] = useState({});
    const [amount, setAmount] = useState(1);
    const [amountInFromCurrency, setAmountInFromCurrency] = useState(true);

    let fromAmount, toAmount;
    if(amountInFromCurrency){
        fromAmount = amount;
        toAmount = (amount * exchangeRate);
        if(toAmount > 0.01){
            toAmount = toAmount.toFixed(2);
        } else{
            toAmount = toAmount.toFixed(5);
        }
    }else{
        toAmount = amount;
        fromAmount = (amount / exchangeRate);
        if(fromAmount > 0.01){
            fromAmount = fromAmount.toFixed(2);
        } else{
            fromAmount = fromAmount.toFixed(5);
        }
    };

    function handleFromCurrencyChange(e) {
        setAmount(e.target.value);
        setAmountInFromCurrency(true);
    }

    function handleToCurrencyChange(e){
        setAmount(e.target.value);
        setAmountInFromCurrency(false);
    }

    useEffect(() => {
        fetch(`${BASE_URL}latest?access_key=${API_KEY}`)
        .then(res => res.json())
        .then(data => {
            const firstCurrency = Object.keys(data.rates)[0];
            setFromCurrency(data.base);
            setToCurrency(firstCurrency);
            setExchangeRate(data.rates[firstCurrency]);
            setExchangeRates(data.rates);
        })
    }, [])

    useEffect(() => {
        fetch(`${BASE_URL}symbols?access_key=${API_KEY}`)
        .then(res => res.json())
        .then(data => {
            setCurrencyOptions(data.symbols);
        })
    }, [])

    return(
        <div className='currency-body'>
            <h1>Currency Converter</h1>
            <CurrencyRow 
                currencyOptions={currencyOptions} 
                selectedCurrency={fromCurrency}
                onChange={e => {
                    setFromCurrency(e.target.value);
                    const EURToFromCurrency = exchangeRates[e.target.value];
                    const EURToToCurrency = exchangeRates[toCurrency];
                    const resultExchangeRate = EURToToCurrency / EURToFromCurrency;
                    setExchangeRate(resultExchangeRate);
                }}
                amount = {fromAmount}
                onAmountChange={handleFromCurrencyChange}
            />
            <div className='equals'>=</div>
            <CurrencyRow 
                currencyOptions={currencyOptions} 
                selectedCurrency={toCurrency}
                onChange={e => {
                    setToCurrency(e.target.value);
                    const EURToFromCurrency = exchangeRates[fromCurrency];
                    const EURToToCurrency = exchangeRates[e.target.value];
                    const resultExchangeRate = EURToToCurrency / EURToFromCurrency;
                    setExchangeRate(resultExchangeRate);
                }}
                amount = {toAmount}
                onAmountChange={handleToCurrencyChange}
            />
            <div className='currency-description'>
                {fromAmount || 'Zero'} {currencyOptions[fromCurrency]} is equal to {toAmount || 'zero'} {currencyOptions[toCurrency]}.
            </div>
        </div>
    )
}

export default Currency;